@import url("https://fonts.googleapis.com/css?family=Nunito:300,400,500,600,700&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;
/* // _general.scss */
/*********************************/
/*             General            */
/*===============================*/
p {
  @apply leading-relaxed;
}

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  @apply leading-normal;
}

::selection {
  @apply bg-violet-600/90 text-white;
}

/********************/
/*     Buttons      */
/*==================*/
/* Note: If you want to add your own colors, you can do so by going to this tailwind.config.js file  */
.btn {
  @apply py-2 px-5 inline-block font-semibold tracking-wide border align-middle transition duration-500 ease-in-out text-base text-center;
  /*  Button icons */
  /* Button Link */
}
.btn.btn-lg {
  @apply py-2.5 px-6 text-lg;
}
.btn.btn-sm {
  @apply py-[5px] px-4 text-sm;
}
.btn.btn-icon {
  @apply p-0 h-10 w-10 inline-flex items-center text-center justify-center text-base;
}
.btn.btn-icon.btn-lg {
  @apply h-12 w-12;
}
.btn.btn-icon.btn-sm {
  @apply h-8 w-8;
}
.btn.btn-link {
  @apply relative p-0 border-none after:content-[""] after:absolute after:h-px after:w-0 after:end-0 after:bottom-0 after:start-0 after:transition-all after:duration-500;
}
.btn.btn-link:hover {
  @apply after:w-full after:end-auto;
}

/********************/
/*    Breadcrumb    */
/*==================*/
.breadcrumb .breadcrumb-item {
  @apply before:content-[""] after:text-sm after:text-black after:dark:text-white after:ps-[5px] last:after:hidden;
}
.breadcrumb .breadcrumb-item:after {
  content: "󰅂" !important;
  font-family: "Material Design Icons";
}
.breadcrumb.breadcrumb-light .breadcrumb-item {
  @apply after:text-white/50;
}

.page-wrapper {
  height: 100vh;
}
.page-wrapper .sidebar-wrapper {
  @apply fixed top-0 -start-[300px] z-999 shadow bg-white dark:bg-slate-900 duration-500 w-[300px] h-screen max-h-screen;
}
@media (max-width: 1024px) {
  .page-wrapper .sidebar-wrapper {
    @apply start-0;
  }
}
@media (max-width: 425px) {
  .page-wrapper .sidebar-wrapper {
    @apply w-[260px];
  }
}
.page-wrapper .sidebar-wrapper ul {
  @apply list-none p-0 m-0;
}
.page-wrapper .sidebar-wrapper a {
  @apply decoration-0;
}
.page-wrapper .sidebar-wrapper .sidebar-content {
  @apply relative overflow-y-auto h-full max-h-full;
}
.page-wrapper .sidebar-wrapper .sidebar-content.desktop {
  @apply overflow-y-hidden;
}
.page-wrapper .sidebar-wrapper .sidebar-brand {
  @apply flex items-center h-[70px] py-2.5 px-5;
}
.page-wrapper .sidebar-wrapper .sidebar-brand > a {
  @apply uppercase font-bold flex-grow duration-500;
}
.page-wrapper .sidebar-wrapper .sidebar-brand > a:focus {
  outline: none !important;
}
.page-wrapper .sidebar-wrapper .sidebar-brand #close-sidebar {
  @apply cursor-pointer text-2xl duration-500;
}
.page-wrapper .sidebar-wrapper .sidebar-menu {
  @apply py-4 px-0;
}
.page-wrapper .sidebar-wrapper .sidebar-menu .header-menu span {
  @apply font-bold text-sm pt-[15px] pb-[5px] px-5 inline-block;
}
.page-wrapper .sidebar-wrapper .sidebar-menu li {
  @apply mx-2 my-1;
}
.page-wrapper .sidebar-wrapper .sidebar-menu li a {
  @apply relative flex items-center w-full decoration-0 py-2 pe-6 ps-5 duration-500;
}
.page-wrapper .sidebar-wrapper .sidebar-menu li a:focus {
  outline: none !important;
}
@media (max-width: 1024px) {
  .page-wrapper .sidebar-wrapper .sidebar-menu li a {
    @apply px-6;
  }
}
@media screen and (max-width: 425px) {
  .page-wrapper .sidebar-wrapper .sidebar-menu li a {
    @apply pe-6 ps-3;
  }
}
.page-wrapper .sidebar-wrapper .sidebar-menu li a:hover > i::before {
  @apply inline-block;
}
.page-wrapper .sidebar-wrapper .sidebar-menu .sidebar-dropdown > a {
  @apply after:absolute after:top-2 after:end-[15px] after:duration-500 after:text-slate-900 dark:after:text-white/50 rtl:after:-rotate-180;
}
.page-wrapper .sidebar-wrapper .sidebar-menu .sidebar-dropdown > a:after {
  content: "󰍟" !important;
  font-family: "Material Design Icons";
}
.page-wrapper .sidebar-wrapper .sidebar-menu .sidebar-dropdown.active > a {
  @apply after:rotate-90 after:end-[17px];
}
.page-wrapper .sidebar-wrapper .sidebar-menu .sidebar-dropdown:hover > a:after, .page-wrapper .sidebar-wrapper .sidebar-menu .sidebar-dropdown.active > a:after {
  @apply text-violet-600 dark:text-white;
}
.page-wrapper .sidebar-wrapper .sidebar-menu .sidebar-dropdown .sidebar-submenu {
  @apply hidden;
}
.page-wrapper .sidebar-wrapper .sidebar-menu .sidebar-dropdown .sidebar-submenu ul {
  @apply py-[5px] px-0;
}
.page-wrapper .sidebar-wrapper .sidebar-menu .sidebar-dropdown .sidebar-submenu ul li {
  @apply text-sm;
}
.page-wrapper .sidebar-wrapper .sidebar-menu .sidebar-dropdown .sidebar-submenu ul li a {
  @apply before:text-slate-900 dark:before:text-white before:me-2 before:text-base before:duration-500 rtl:before:rotate-180;
}
.page-wrapper .sidebar-wrapper .sidebar-menu .sidebar-dropdown .sidebar-submenu ul li a:before {
  content: "󰮺" !important;
  font-family: "Material Design Icons";
}
.page-wrapper .sidebar-wrapper .sidebar-menu .sidebar-dropdown .sidebar-submenu ul li a .badge,
.page-wrapper .sidebar-wrapper .sidebar-menu .sidebar-dropdown .sidebar-submenu ul li a .label {
  @apply float-right mt-0;
}
.page-wrapper .sidebar-wrapper .sidebar-menu ul li a span.label, .page-wrapper .sidebar-wrapper .sidebar-menu ul li a span.badge {
  float: right;
  margin-top: 8px;
  margin-left: 5px;
}
.page-wrapper .sidebar-wrapper .sidebar-brand > a,
.page-wrapper .sidebar-wrapper .sidebar-menu li a {
  @apply text-slate-900 dark:text-white/50 font-semibold;
}
.page-wrapper .sidebar-wrapper .sidebar-menu li:hover > a,
.page-wrapper .sidebar-wrapper .sidebar-menu li.active > a,
.page-wrapper .sidebar-wrapper .sidebar-menu li:active > a,
.page-wrapper .sidebar-wrapper .sidebar-menu li:active > a:before,
.page-wrapper .sidebar-wrapper .sidebar-menu li:hover > a:before,
.page-wrapper .sidebar-wrapper .sidebar-menu li.active > a:before,
.page-wrapper .sidebar-wrapper .sidebar-menu .sidebar-dropdown.active > a,
.page-wrapper .sidebar-wrapper .sidebar-brand > a:hover {
  @apply bg-slate-50 dark:bg-slate-800 text-violet-600 dark:text-white rounded-md !important;
}
.page-wrapper .page-content {
  @apply inline-block w-full ps-0 overflow-x-hidden duration-500;
}
.page-wrapper .page-content .top-header {
  @apply fixed top-0 inset-x-0 z-999 max-h-[70px] duration-500;
}
.page-wrapper .page-content .top-header .header-bar {
  @apply relative justify-between items-center p-6 bg-white dark:bg-slate-900 h-[70px] shadow dark:shadow-gray-700;
}
@media (max-width: 425px) {
  .page-wrapper .page-content .top-header .header-bar {
    @apply py-6 px-3;
  }
}
.page-wrapper .page-content .layout-specing {
  min-height: calc(100vh - 67px) !important;
  @apply relative pt-[94px] px-3 pb-6 !important;
}
@media (max-width: 425px) {
  .page-wrapper .page-content .layout-specing {
    @apply px-0 !important;
  }
}
@media screen and (min-width: 1025px) {
  .page-wrapper.toggled .page-content {
    @apply ps-[300px];
  }
}
.page-wrapper.toggled .top-header {
  @apply start-[301px];
}
@media (max-width: 1024px) {
  .page-wrapper.toggled .top-header {
    @apply start-0;
  }
}
.page-wrapper.toggled .sidebar-wrapper {
  @apply start-0;
}
@media (max-width: 1024px) {
  .page-wrapper.toggled .sidebar-wrapper {
    @apply -start-[300px];
  }
}

.sidebar-footer {
  @apply absolute w-full bottom-0 start-0 end-0 py-3 ps-6 pe-5 shadow dark:shadow-gray-700 h-[51px];
}
@media (max-width: 425px) {
  .sidebar-footer {
    @apply py-2 px-3 !important;
  }
}

/*********************************/
/*             Helper            */
/*===============================*/
/* Switcher */
.label .ball {
  transition: transform 0.2s linear;
  @apply translate-x-0;
}

.checkbox:checked + .label .ball {
  @apply translate-x-6;
}

/****************/
/*  simplebar   */
/****************/
.simplebar-track.simplebar-vertical {
  @apply w-2 !important;
}
.simplebar-track.simplebar-vertical .simplebar-scrollbar {
  @apply before:w-1 before:bg-slate-300 dark:before:bg-slate-700 !important;
}

/*********************************/
/*         Countdown             */
/*===============================*/
#token-sale .count-number .count-head {
  @apply relative block uppercase md:-mt-5 md:text-base text-sm leading-[1px] font-bold;
}

#countdown .count-down .count-number {
  @apply text-[40px] leading-[110px] h-[130px] w-[130px] rounded-full shadow-md bg-white/10;
  backdrop-filter: blur(5px);
}
#countdown .count-down .count-head {
  @apply relative block -translate-y-[25px] uppercase text-sm tracking-[1px];
}

/* //maintenance */
#maintenance .indicator {
  @apply text-lg;
}

/*********************************/
/*           Portfolio           */
/*===============================*/
.container-filter-box li.active, .container-filter-box li:hover {
  @apply text-violet-600 dark:text-slate-200 border-violet-600 dark:border-slate-500 !important;
}

/*********************************/
/*         Tobii Lightbox        */
/*===============================*/
/* Tobii Lighbox */
.tobii > button.tobii__close svg,
.tobii > button.tobii__prev svg,
.tobii > button.tobii__next svg {
  @apply h-9 w-auto;
}

.tobii__counter {
  @apply text-base;
}

.tobii-zoom {
  @apply block;
}

.tobii-zoom__icon {
  @apply hidden;
}

#grid {
  @apply p-0;
}

@media (max-width: 640px) {
  [data-type] iframe, [data-type] video {
    width: 500px;
    height: 300px;
  }
}
@media (max-width: 425px) {
  [data-type] iframe, [data-type] video {
    width: 360px;
    height: 260px;
  }
}
@media (max-width: 375px) {
  [data-type] iframe, [data-type] video {
    width: 100%;
    height: auto;
  }
}